<template>
  <div class='editmenu'>
    <div>
      <CommonHeader></CommonHeader>
    </div>
    <div>
      <Set></Set>
    </div>
    <!--主要内容-->
    <div class='content'>
      <div class='content-left'>
        <div class='content-left-t'>角色列表</div>
        <div class='content-left-b'>
          <ul>
            <li class='personLi' v-for='(item,index) in roleList' :class="{'current':index==0}" @click='getCurrent(index,item.id)'>{{item.name}}</li>

          </ul>
        </div>
      </div>
      <div class='content-right'>
        <div class='content-right-t'>
          <div class='c-r-t-emText'>角色菜单配置</div>
          <span class='border-cover'></span>
          <div class='exit-btn'>
            <button type="button" class="btn btn-primary upload-btn pull-right" @click="getCheckedKeys">保存</button>
          </div>
        </div>
        <div class='content-right-b'>
          <el-tree :data="menuList" show-checkbox default-expand-all='' node-key="id" highlight-current=""
                   :props="defaultProps" class="menuTree" ref="tree" >
          </el-tree>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import CommonHeader from '../public/header.vue';
  import Set from '../public/set.vue';
  import $ from 'jquery'
  export default {
    data() {
      return {
        Authorization: '',
        roleList: [],
        menuList: [],
        selectKeys:[],//获取勾选的节点
        setSelectKeys:[],//设置勾选的节点
        defaultProps: {
          children: 'children',
          label: 'name'
        }
      }
    },
    mounted() {
      // 员工列表高度
      this.getClientHeight()
      // 获取角色列表
      this.getRole()
      this.getMenus();//查询菜单
    },
    methods: {
      // 获取角色列表
      getRole() {
        this.Authorization = localStorage.getItem('access_token')
        var url = '/api/uaa/roles/selectRoles'
        console.log(this.Authorization)
        this.$http.post(url, {}, {
          headers: {
            Authorization: 'bearer ' + this.Authorization
          },
          emulateJSON: true
        }).then(function (res) {
          this.roleList = res.body
          console.log(res.body)
        })
      },
      getMenus: function () {
        var url = "/api/uaa/menu/showMenu";
        var token = localStorage.getItem('access_token');
        this.$http.get(url, {
          headers: {
            Authorization: 'bearer ' + token
          },
        }).then(function (res) {
          //赋值
          this.menuList=res.body;

        },function (err) {
          console.log(err);
        })
      },
      getCheckedKeys() {
        //获取勾选的节点
        console.log(this.$refs.tree.getCheckedKeys());
        this.selectKeys = this.$refs.tree.getCheckedKeys();
      },
      setCheckedKeys() {
        //设置勾选的节点
        this.$refs.tree.setCheckedKeys(this.setSelectKeys);
      },
      resetChecked:function() {
        this.$refs.tree.setCheckedKeys([]);
      },
      clickRole: function (role) {
//        this.isActive = true;
        this.selectRoleId = role.id;
        //角色点击,根据角色加载菜单
        var url = "/api/uaa/roles/selectMenuByRoleId?roleId="+role.id;
        var token = localStorage.getItem('access_token');
        this.$http.get(url, {
          headers: {
            Authorization: 'bearer ' + token
          },
        }).then(function (res) {
          //赋值
          this.setSelectKeys=res.body;
          this.setCheckedKeys();//设置勾选

        },function (err) {
          console.log(err);
        })
      },
      saveRoleMenu() {
        console.log(this.setSelectKeys)
        // if(this.selectRoleId === null || this.selectRoleId === "") {
        //   this.warn("请先选择角色");
        //   return;
        // }
        // this.getCheckedKeys();//获取勾选的节点
        // if(this.selectKeys == null || this.selectKeys.length < 1) {
        //   this.warn("请先勾选菜单");
        //   return;
        // }
      },
      // 样式变化及列表数据的渲染
      getCurrent(index,id) {
        $('.personLi').eq(index).addClass('current');
        $('.personLi').eq(index).siblings().removeClass('current')
      },
      // 判断屏幕的大小，获取患者第二级导航的高度
      getClientHeight() {
        $('.content-left-b').css({
          height: document.documentElement.clientHeight - 30 + 'px'
        })
        $(window).resize(function () {
          $('.content-left-b').css({
            height: document.documentElement.clientHeight - 30 + 'px'
          })
        })

      }
    },
    components: {
      Set: Set,
      CommonHeader: CommonHeader
    }
  }

</script>
<style scoped>
  /*大局样式*/

  .content {
    margin: 50px 0px 0px 180px;
    padding-left: 180px;
  }




  /*左边部分*/

  .content-left {
    position: fixed;
    top: 50px;
    left: 180px;
    width: 180px;
    background: white;
  }

  .content-left .content-left-t {
    padding-left: 20px;
    height: 70px;
    line-height: 70px;
    font-size: 14px;
    background: #D9DEE4;
    font-weight: bold;
  }

  .content-left .content-left-b {
    padding-bottom: 80px;
    overflow: auto;
    background: #EAEDF1;
  }

  .content-left .content-left-b li {
    height: 40px;
    line-height: 40px;
    padding-left: 30px;
    font-size: 14px;
  }

  .content-left .content-left-b li:hover {
    background: white;
  }

  .content-left .content-left-b li.current {
    background: white;
  }

  .personLi {
    cursor: pointer;
  }


  /*右边样式*/

  .content-right {
    width: 100%;
    min-width: 800px;
    padding: 0px 15px;
  }

  .content-right .c-r-t-emText {
    padding: 8px 8px 8px 8px;
  }

  .content-right .content-right-t {
    padding: 16px 0px;
    font-size: 14px;
    height: 70px;
    background: white;
    position: relative;
    border-bottom: 1px solid #ddd;
  }

  .content-right-t .border-cover {
    display: block;
    width: 1px;
    height: 14px;
    background: #6886d4;
    position: absolute;
    left: 1px;
    top: 26px;
  }

  .content-right-t .exit-btn {
    position: absolute;
    top: 16px;
    right: 0px;
  }
</style>




