<script>
import $ from 'jquery'
import bus from '../../untils/bus.js'
export default {
    name: 'Set',
    created() {
        this.initialization()
    },
    mounted() {
        if (localStorage.getItem('childTree')) {
            this.childTree = JSON.parse(localStorage.getItem('childTree'))
        } else {
            this.getTreeTab()
        }
        var that = this
        bus.$on('deliveryChild', function(child) {
            if (child.id == 102) {
                that.systemManagement = true;
                // 状态缓存
                localStorage.setItem('systemManagement', that.systemManagement)
            } else {
                that.systemManagement = false;
                // 状态缓存
                localStorage.setItem('systemManagement', that.systemManagement)
            }
            that.childTree = child.children
            that.childTree=that.sort(that.childTree)
            localStorage.setItem('childTree', JSON.stringify(child.children))
            // 样式的状态缓存
            that.twoIsShow = JSON.parse(localStorage.getItem('twoIsShow')) || [false, false, false]
            that.twoIsShow2 = JSON.parse(localStorage.getItem('twoIsShow2')) || [false, false, false]
        })
        // 解决高度不固定的问题
        this.$nextTick(function() {

        })
    },
    data: function() {
        return {
            childTree: [],
            twoIsShow: [],
            twoIsShow2: [],
            systemManagement: true,
        }
    },
    methods: {
        // 高度
        twoclick(index) {
            this.twoIsShow[index] = !this.twoIsShow[index]
            this.twoIsShow = this.twoIsShow.concat()
            localStorage.setItem('twoIsShow', JSON.stringify(this.twoIsShow))


        },
        twoclick2(index) {
            this.twoIsShow2[index] = !this.twoIsShow2[index]
            this.twoIsShow2 = this.twoIsShow2.concat()
            localStorage.setItem('twoIsShow2', JSON.stringify(this.twoIsShow2))

        },
        // 路由
        jumpRouter(id) {
            if (id == 32) { this.$router.push({ name: 'Expert' }) }//预约协诊
            if (id == 31) { this.$router.push({ name: 'Patients' }) }//患者管理
            if (id == 34) { this.$router.push({ name: 'Account' }) }//我的钱包
            if (id == 26) { this.$router.push({ name: 'Employees' }) }//医生管理
            if (id == 33) { this.$router.push({ name: 'AssistOrders' }) }//订单管理
            if (id == 30) { this.$router.push({ name: 'AssistOrderIng' }) }//今日协诊
            if (id == 19) { this.$router.push({ name: 'dataDictionary' }) }//字典配置
            if (id == 15) { this.$router.push({ name: 'mentConfig' }) }//菜单管理
            if (id == 16) { this.$router.push({ name: 'Role' }) }//角色管理
            if (id == 14) { this.$router.push({ name: 'User' }) }//用户管理
            if (id == 17) { this.$router.push({ name: 'RoleMenu' }) }//角色菜单配置
            if (id == 35) { this.$router.push({ name: 'CashWithdrawal' }) }//财务提现处理
            if (id == 28) { this.$router.push({ name: 'Scheduling' }) }//专家排班管理
            if (id == 29) { this.$router.push({ name: 'Organization' }) }//医疗机构管理
            if (id == 27) { this.$router.push({ name: 'docterAndExpert' }) }//医生/专家管理
            if (id == 22) { this.$router.push({ name: 'OrderLog'})}//订单操作日志
        },
        // tab
        getTreeTab() {
            // 开发阶段
            // var url = 'api/uaa/menu/showMenu';
            // var childTree;
            // this.Authorization = localStorage.getItem('access_token');
            // this.$http.get(url, {
            //     headers: {
            //         Authorization: 'bearer ' + this.Authorization
            //     }
            // }).then(function (res) {
            //     // 版本迭代时，需要修改childTree字符串
            //     if (localStorage.getItem('childTree')) {
            //         this.childTree = JSON.parse(localStorage.getItem('childTree'))
            //     }
            //     else {
            //         this.childTree = res.body.reverse()[0].children
            //         localStorage.setItem('childTree', JSON.stringify(this.childTree))
            //     }
            // }, function (err) { })
            // 上线阶段
            var url = '/api/uaa/roleMenu/selectMenuOBJByRoleId'
            var user = JSON.parse(localStorage.getItem('user'))
            this.Authorization = localStorage.getItem('access_token')
            this.$http.get(url, {
                headers: {
                    Authorization: 'bearer ' + this.Authorization
                },
                params: {
                    roleId: user.rid
                }
            }).then(function(res) {
                // 版本迭代时，需要修改childTree字符串
                if (localStorage.getItem('childTree')) {
                    this.childTree = JSON.parse(localStorage.getItem('childTree'))
                }
                else {
                    this.childTree = res.body[0].children
                    this.childTree=this.sort(this.childTree)
                    localStorage.setItem('childTree', JSON.stringify(this.childTree))
                }
            }, function(err) { })
        },
        // 初始化数据
        initialization() {
            // //  模块管理数据
            if (localStorage.getItem('systemManagement')) {
                this.systemManagement = JSON.parse(localStorage.getItem('systemManagement'))
            } else {
                this.systemManagement = true
            }
            var user = JSON.parse(localStorage.getItem('user'))
            if (user.rid == '1314') {
                // //  改变 Class
                this.twoIsShow = JSON.parse(localStorage.getItem('twoIsShow')) || [false, false, true]
                this.twoIsShow2 = JSON.parse(localStorage.getItem('twoIsShow2')) || [false, false, true]
            } else if (user.rid = '1315') {
                // //  改变 Class
                this.twoIsShow = JSON.parse(localStorage.getItem('twoIsShow')) || [false, true, false]
                this.twoIsShow2 = JSON.parse(localStorage.getItem('twoIsShow2')) || [false, true, false]
            } else {
                this.twoIsShow = JSON.parse(localStorage.getItem('twoIsShow')) || [true, false, false]
                this.twoIsShow2 = JSON.parse(localStorage.getItem('twoIsShow2')) || [true, false, false]
            }

        },
        // 排
        sort(childTree) {
            // 二排
            for (var i = 0; i < childTree.length - 1; i++) {
                for (var j = 0; j < childTree.length - 1; j++) {
                    if (childTree[j].sortId > childTree[j + 1].sortId) {
                        var t = childTree[j];
                        childTree[j] = childTree[j + 1];
                        childTree[j + 1] = t
                    }
                }
            }
            // // 三排
            for (var i = 0; i < childTree.length; i++) {
                for (var j = 0; j < childTree[i].children.length - 1; j++) {
                    for (var k = 0; k < childTree[i].children.length - 1; k++) {
                        if (childTree[i].children[k].sortId > childTree[i].children[k + 1].sortId) {
                            var t = childTree[i].children[k]
                            childTree[i].children[k] = childTree[i].children[k + 1]
                            childTree[i].children[k + 1] = t
                        }
                    }
                }
            }
            return childTree
        }
    }
}
</script>



