</template>
<style scoped>
  .matchlessHeader {
    height: 50px;
    width: 100%;
    z-index: 111;
    background: #23262E;
    min-width: 1200px;
    position: fixed;
    left: 0px;
    top: 0px;
    color: white;
  }

  .matchlessHeader .img {
    width: 230px;
    height: 100%;
    padding: 10px 60px 10px 40px;
    color: #fff;
    line-height: 30px;
  }

  .matchlessHeader .img img {
    width: 30px;
  }

  .matchlessHeader .img span {
    margin-left: 10px;
    font-size: 18px;
    font-weight: bold;
  }

  .chooseNav ul .navLi {
    font-size: 16px;
    color: #fff;
    cursor: pointer;
    height: 50px;
    line-height: 50px;
    margin-right: 20px;
  }

  .userMes .userHelp,
  .userMes .userName,
  .userMes .userNews {
    padding: 15px;
    cursor: pointer;
  }

