<script>
  import massgeIcon from '../../assets/massg.png';
  import userMag from '../../assets/userMage.png';
  import publicFn from '../../untils/Public.js';
  import bus from '../../untils/bus.js'
  export default {
    name: 'CommonHeader',
    created() {
      // 判断是否已经登录
      if (!localStorage.getItem('access_token')) {
        this.$router.push({ name: "index" })
      }
      this.userText();
      // 树形结构(开发阶段)
      // this.getTreeTab()
      // 根据角色拿到树形结构(上线阶段)
      this.getRoleTreeTab()

      // 时限
      var tokenTimer = JSON.parse(localStorage.getItem('tokenTimer'))
      if (tokenTimer.timeLimit + tokenTimer.nowTime <= new Date()) {
        localStorage.removeItem('access_token');
        this.$router.push({ name: "index" })
      } else {
      }

    },
    data: function() {
      return {
        usermes: {},
        massge: {
          icon: massgeIcon,
          cunt: 0,
          list: []
        },
        treeTab: [],
        Authorization: ''
      };
    },
    methods: {
      handleCommand(command) {
        if (command == 'logout') {
          this.logout()
        } else if (command == 'modifyPw') {
          this.modifyPw();
        }
      },
      //退出登錄
      logout() {
        localStorage.removeItem('access_token');
        localStorage.removeItem('webim_');
        localStorage.removeItem('easemob');
        this.$store.commit('lognOut', null);
        conn.close()
        this.$router.push({ name: 'index' })
      },
      // 用戶信息
      userText() {
        //  var url='api/organization/employees/'+userId
        var url = '/api/uaa/eztUsers/profile';
        var token = localStorage.getItem('access_token');
        this.$http.get(url, {
          headers: {
            Authorization: 'bearer ' + token
          }
        }).then(function(res) {
          this.usermes = res.body;
          var webim_ = localStorage.getItem('webim_');
          if (res.body.easemobUser) {
            if (webim_) {
              this.longByHxForToken(res.body.easemobUser.username, webim_)
              //            this.longByHx(res.body.easemobUser.username, res.body.easemobUser.password);
            }
            if (!webim_) {
              this.longByHx(res.body.easemobUser.username, res.body.easemobUser.password);
            }
          }
          this.$emit('getUserMes', res.body);
          publicFn.publicLo.sessionStorageSav("userBySelf", res.body);
          this.$store.commit('pushUser', res.body);
        }, err => { })
      },
      // 修改密码
      modifyPw() {
        this.$router.push({ name: 'ModifyPW' })
      },
      // 树形导航（开发阶段）
      getTreeTab() {
        var url = 'api/uaa/menu/showMenu';
        this.Authorization = localStorage.getItem('access_token');
        this.$http.get(url, {
          headers: {
            Authorization: 'bearer ' + this.Authorization
          }
        }).then(function(res) {
          this.treeTab = res.body
          this.treeTab=this.sort(this.treeTab)
        }, function(err) { })
      },
      longByHx: function(name, password) {
        var that = this;
        var options = {
          apiUrl: WebIM.config.apiURL,
          user: name,
          pwd: password,
          appKey: WebIM.config.appkey,
          success: function(token) {
            var token = token.access_token;
            localStorage.setItem('webim_', token);
          },
        };
        localStorage.setItem('easemob', JSON.stringify({ name: name, password: password }));
        conn.open(options);
      },
      longByHxForToken: function(name, token) {
        var options = {
          apiUrl: WebIM.config.apiURL,
          user: name,
          accessToken: token,
          appKey: WebIM.config.appkey,
          success: function(token) {
          },
        };
        conn.open(options);
      },
      // 根据角色拿到树形导航（上线阶段）
      // getRoleTreeTab() {
      //   var url = '/api/uaa/roleMenu/selectMenuOBJByRoleId'
      //   var user = JSON.parse(localStorage.getItem('user'))
      //   this.Authorization = localStorage.getItem('access_token')
      //   this.$http.get(url, {
      //     headers: {
      //       Authorization: 'bearer ' + this.Authorization
      //     },
      //     params: {
      //       roleId: user.rid
      // getTreeTab(){
      //   var url='api/uaa/menu/showMenu'
      //   this.Authorization=localStorage.getItem('access_token')
      //   this.$http.get(url,{
      //     headers:{
      //       Authorization:'bearer '+this.Authorization
      //     }
      //   }).then(function(res){
      //     this.treeTab=res.body.reverse()
      //   },function(err){})
      // },
      // 根据角色拿到树形导航（上线阶段）
      getRoleTreeTab() {
        var url = '/api/uaa/roleMenu/selectMenuOBJByRoleId';
        var user = JSON.parse(localStorage.getItem('user'));
        this.Authorization = localStorage.getItem('access_token');
        this.$http.get(url, {
          headers: {
            Authorization: 'bearer ' + this.Authorization
          },
          params: {
            roleId: user.rid
          }
        }).then(function(res) {
          this.treeTab = res.body
          this.treeTab=this.sort(this.treeTab)
        })
      },
      DeliveryTreeTab(child) {
        bus.$emit('deliveryChild', child)
      },
      sort(childTree) {
        for (var i = 0; i < childTree.length - 1; i++) {
          for (var j = 0; j < childTree.length - 1; j++) {
            if (childTree[j].sortId > childTree[j + 1].sortId) {
              var t = childTree[j];
              childTree[j] = childTree[j + 1];
              childTree[j + 1] = t
            }
          }
        }
        return childTree
      }
    }
  }
</script>



